<script setup lang="ts">
import {AppGrid, routerLinkIdPreset} from '@/components/grid';
import {MessageService} from '@/services/message.service';
import {pickGridColumns} from '@/logic/common';
import {messageGridConfig} from '@/logic/message-grid-config';

const config = pickGridColumns(
  messageGridConfig,
  ['orderId', 'displayName', 'displayText', 'createdAt'],
  {
    title: 'Все сообщения',
    getData: () => useService(MessageService).getAllMessages(),
    columns: {
      orderId: routerLinkIdPreset('/moder/all-order-info?orderId='),
    },
  },
);
</script>

<template>
  <AppGrid :config="config" />
</template>
